.footer {
  --footer-height: 555px;

  position: sticky;
  bottom: 0;
  z-index: -1;

  height: var(--footer-height);

  background: url("../assets/footer.png") center/cover no-repeat;

  @include onLaptop {
    --footer-height: 480px;
  }

  @include onMobile {
    --footer-height: 200px;
  }
}
